import {
  Component,
  Prop,
  State,
  Event,
  EventEmitter,
  h,
  Host,
  Watch,
} from '@stencil/core';

/**
 * @slot - Content displayed when the accordion item is expanded
 */
@Component({
  tag: 'bh-accordion-item',
  styleUrl: 'bh-accordion-item.css',
  shadow: true,
})
export class BhAccordionItem {
  /**
   * Unique identifier for this accordion item
   */
  @Prop() value!: string;

  /**
   * The header/title text for the accordion item
   */
  @Prop() header!: string;

  /**
   * Whether this accordion item is currently expanded
   */
  @Prop({ mutable: true, reflect: true }) expanded: boolean = false;

  /**
   * Whether this accordion item is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Internal state for animation
   */
  @State() isAnimating: boolean = false;

  /**
   * Emitted when the accordion item is toggled
   */
  @Event({ bubbles: true, composed: true })
  bhAccordionToggle!: EventEmitter<{ value: string; expanded: boolean }>;

  /**
   * Emitted when the accordion header is clicked
   */
  @Event({ bubbles: true, composed: true })
  bhAccordionClick!: EventEmitter<string>;

  @Watch('expanded')
  handleExpandedChange(): void {
    this.isAnimating = true;
    setTimeout(() => {
      this.isAnimating = false;
    }, 200);
  }

  private handleHeaderClick = (): void => {
    if (this.disabled) return;

    this.expanded = !this.expanded;
    this.bhAccordionClick.emit(this.value);
    this.bhAccordionToggle.emit({
      value: this.value,
      expanded: this.expanded,
    });
  };

  private handleKeyDown = (event: KeyboardEvent): void => {
    if (this.disabled) return;

    if (event.key === 'Enter' || event.key === ' ') {
      event.preventDefault();
      this.handleHeaderClick();
    }
  };

  render() {
    const hostClasses = {
      'accordion-item': true,
      'accordion-item-expanded': this.expanded,
      'accordion-item-disabled': this.disabled,
      'accordion-item-animating': this.isAnimating,
    };

    const iconName = this.expanded ? 'expand_less' : 'expand_more';

    return (
      <Host class={hostClasses}>
        <div
          class="accordion-header"
          part="header"
          role="button"
          tabindex={this.disabled ? -1 : 0}
          aria-expanded={this.expanded ? 'true' : 'false'}
          aria-disabled={this.disabled ? 'true' : 'false'}
          aria-controls={`content-${this.value}`}
          onClick={this.handleHeaderClick}
          onKeyDown={this.handleKeyDown}
        >
          <div class="accordion-header-content">
            <span class="accordion-title" part="title">
              {this.header}
            </span>
          </div>
          <span class="accordion-icon material-symbols-outlined" part="icon">
            {iconName}
          </span>
        </div>
        <div
          id={`content-${this.value}`}
          class="accordion-content"
          part="content"
          role="region"
          aria-labelledby={`header-${this.value}`}
          aria-hidden={!this.expanded ? 'true' : 'false'}
        >
          <div class="accordion-content-inner">
            <slot />
          </div>
        </div>
      </Host>
    );
  }
}
